import { __ } from '@wordpress/i18n';
import { useBlockProps } from '@wordpress/block-editor';
import './editor.scss';
import { ReactComponent as EmptyIcon } from './img/empty-state.svg';
import { Button } from '@wordpress/components';

/**
 * Empty state placeholder.
 * 
 * @return {Element} Element to render.
 */
export default function Edit() {
	
	const openPattern = () => {
		wp.data.dispatch( 'core/edit-post' ).setIsInserterOpened( { rootClientId: undefined, insertionIndex: 5, filterValue: "flux" } );
		clearBlock();
	}

	const clearBlock = () => {
		wp.data.dispatch( 'core/block-editor' ).resetBlocks([]);
	}

	return (
		<div { ...useBlockProps() }>
			<div className="fce-empty">
				<div className="fce-empty__icon">
					<EmptyIcon/>
				</div>
				<div className="fce-empty__buttons">
					<Button variant="primary" onClick={ openPattern }>
						{__( '+ Add your first pattern', 'flux-checkout' )}
					</Button>					
				</div>
				<Button variant="link" className="fce-empty__skip" onClick={ clearBlock }>
					{__(
						'Or skip this step and add your own content',
						'flux-checkout'
					)}
				</Button>
			</div>	
		</div>
	);
}
